import React from "react"
import { Avatar, FontIcon, List, ListItem } from "react-md"
import { withPrefix } from 'gatsby'

const Paper = () => {
  const papers = [
    {
      id: 1,
      icon: "folder",
      primaryText: "Implementation of Nurturing Motion Training Program to Improve Fine Motor Skills Down Syndrome Children",
      secondaryText: "Putri Nindya Agustin, Marshel Muhamad Farhan, Yuli Insyirah, Dea Marlis Apriliani & Sisriadini Alamsyah Sidiq",
      component: "a",
      href: "/pdf/2019/test.pdf",
    },
    {
      id: 2,
      icon: "folder",
      primaryText: "Proceeding 2019",
      secondaryText:
        "Elevating Innovation for Sustainable Development of Special Needs Education",
      component: "a",
      href: "/pdf/2019/test.pdf",
    },
  ]

  return (
    <List>
      {papers.map(paper => (
        <ListItem
          key={paper.id}
          leftAvatar={<Avatar icon={<FontIcon>{paper.icon}</FontIcon>} />}
          primaryText={paper.primaryText}
          secondaryText={paper.secondaryText}
          component={paper.component}
          href={withPrefix(paper.href)}
          rel="noopener noreferrer"
        />
      ))}
    </List>
  )
}

export default Paper
